<template>
  <section id="flash">
    <TransitionGroupTranslate class="container">
      <div
        v-for="flash in appFlashMessages"
        :key="flash.id"
        class="flash-message"
        :class="flash.variant"
      >
        <div class="message">{{ flash.message }}</div>
        <div class="close">
          <a href="#" @click.prevent="close(flash)"><i class="fas fa-times"></i></a>
        </div>
      </div>
    </TransitionGroupTranslate>
  </section>
</template>
<script>
import { mapState, mapActions } from 'vuex';

import TransitionGroupTranslate from '../utils/TransitionGroupTranslate';

export default {
  name: 'FlashMessages',
  components: { TransitionGroupTranslate },
  data() {
    return {
      localMessages: []
    };
  },
  props: {
    context: {
      type: String,
      required: false
    }
  },
  watch: {
    $route() {
      this.ActionClearOldFlash();
    }
  },
  computed: {
    ...mapState('app', ['appFlashMessages']),
    show() {
      return this.appFlashMessages.length > 0;
    }
  },
  methods: {
    ...mapActions('app', ['ActionRemoveFlash', 'ActionClearOldFlash']),
    close(flash) {
      this.ActionRemoveFlash(flash);
    }
  },
  beforeUnmount() {
    //this.ActionAppClearFlashMessages();
  }
};
</script>

<style lang="scss" src="./FlashMessages.scss" />
