import { createRouter, createWebHistory } from 'vue-router';

//import beforeEachPwa from './beforeEachPwa';
//import beforeEachAuth from './beforeEachAuth';
//import beforeEachGuard from './beforeEachGuard';
import routes from './routes';

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to, from, savedPosition) {
    let position = savedPosition ? savedPosition : { x: 0, y: 0 };
    //if (to.hash) position = { selector: to.hash };

    return new Promise(resolve => {
      setTimeout(() => {
        resolve(position);
      }, 500);
    });
  }
});

//router.beforeResolve(???);

//router.beforeEach(beforeEachPwa);
//router.beforeEach(beforeEachAuth);
//router.beforeEach(beforeEachGuard);

//router.afterEach(???)

export default router;
