<template>
  <div class="scroll-progress">
    <div
      class="scroll-progress-bar gradbar"
      :style="{ width: `${scroll}%` }"
    ></div>
  </div>
</template>
<script>
export default {
  name: 'ScrollProgress',
  data() {
    return {
      scroll: 0
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
  },
  beforeUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
      var winScroll =
        document.body.scrollTop || document.documentElement.scrollTop;
      var height =
        document.documentElement.scrollHeight -
        document.documentElement.clientHeight;
      //window.scrollY

      this.scroll = Math.round((winScroll / height) * 100);
    }
  }
};
</script>

<style lang="scss">
@import '@/assets/scss/abstract';

$component-name: 'flex-menu';
/*
$gradbar-color1: #ee7752 !default;
$gradbar-color2: #e73c7e !default;
$gradbar-color3: #d54723 !default;
$gradbar-color4: #23d5ab !default;
*/
/* ==|== Scroll-Progress ==========================================
    GERAL
   ================================================================ */

.#{$component-name} {
  margin-bottom: 4px;
}

.#{$component-name}-bar {
  @include use-transition(get-transition(default));
}

/* ==|== Scroll-Progress ==========================================
    GRADBAR
   ================================================================ */
.gradbar {
  width: 0%;
  height: 5px;
  @include use-background-linear-gradient(
    -45deg,
    400% 400%,
    get-color(secondary),
    get-color(primary),
    get-color(dark),
    get-color(clean)
  );
  @include use-animation(gradbar, 10s ease infinite);
}

@include create-animation(gradbar) {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
</style>
