import axios from 'axios';

import { BASE_URL } from '../config';
import store from '@/store';
import router from '@/router';
import services from './services';

import { flash } from '@/resources';
import * as storage from '@/modules/user/storage';

const http = axios.create({
  baseURL: BASE_URL
});

http.defaults.timeout = 5 * 1000;

// Request interceptor
http.interceptors.request.use (
  config => {
    const token = store.state.user.userToken;
    //const token = storage.getLocalToken();
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    //config.headers['Content-Type'] = 'application/json';
    //config.headers['Content-Type'] = 'application/x-www-form-urlencoded';
    //config.headers['Accept'] = 'application/json';
    return config;
  },
  error => {
    Promise.reject(error);
  }
);

// Response interceptor
http.interceptors.response.use(
  function(res) {
    //return Promise.reject("ERROR SAMPLE");

    //if (res.config.url==='/user') return Promise.resolve(res);
    return res;
  },
  async function(err) {
    //console.log('Interceptor: error', err.code, err.message);

    if (err.message === 'Network Error' || err.code === 'ECONNABORTED') {
      if (store.state.main.mainOnline) {
        store.dispatch('app/ActionOffline');
      }
    } else if (err.response) {
      const error = err.response;
      const originalRequest = err.config;

      // usuário não autorizado
      if (error.status === 401) {
        //console.log("url: ", originalRequest.url);

        if (originalRequest.url === '/user/refresh-token') {
          await store.dispatch('user/ActionSessionExpired');
          //console.log("venceu mesmo - Rota", router.to.name);
          //router.push({ name: 'login', query: { r: router.to.name } });
          router.push({ name: 'login' });
        } else if (error.data.key === 'TOKEN_EXPIRED') {
          const refreshToken = storage.getLocalRefreshToken();

          return services.user
            .refreshToken(refreshToken)
            .then(async res => {
              await store.dispatch('user/ActionSetToken', res.data);
              store.dispatch(
                'app/ActionAddFlash',
                flash.user.FLASH_TOKEN_REFRESH,
                { root: true }
              );
              return http(originalRequest);
            })
            .catch(err => {
              return err;
            });
        } else {
          store.dispatch('user/ActionLogout');
        }
      }
    }

    return Promise.reject(err);
  }
);

export { http };

export default services;
