import * as types from './mutation-types';

export default {
  [types.MUTATION_ONLINE_REQUEST](state) {
    state.appLoading = true;
    state.appOnline = false;
    state.appError = undefined;
  },

  [types.MUTATION_ONLINE_SUCCESS](state) {
    state.appLoading = false;
    state.appOnline = true;
    state.appError = undefined;
  },

  [types.MUTATION_ONLINE_FAIL](state, payload) {
    state.appLoading = false;
    state.appOnline = false;
    state.appError = payload.error;
  },

  [types.MUTATION_OFFLINE](state) {
    state.appLoading = false;
    state.appOnline = false;
    state.appError = undefined;
  },

  [types.MUTATION_ADD_FLASH](state, payload) {
    state.appFlashMessages.push(payload);
  },

  [types.MUTATION_SET_FLASH](state, payload) {
    state.appFlashMessages = payload;
  },

  [types.MUTATION_ADD_TOAST](state, payload) {
    state.appToastMessages = [payload, ...state.appToastMessages];
  },

  [types.MUTATION_SET_TOAST](state, payload) {
    state.appToastMessages = payload;
  },

  [types.MUTATION_SET_OFFCANVAS](state, payload) {
    state.appOffCanvas = payload;
  } 

};
