export default [
  {
    path: "/home",
    name: "home",
    component: () =>
      import(/* webpackChunkName: "module_app" */ "./views/Home"),
    meta: {
      showNavHeader: true,
      showNavFooter: true,
      name: "Home",
      order: 1,
      icon: "fas fa-home",
      description: "home page"
    }
  },
  {
    path: "/",
    redirect: { name: "home" }
  },
  {
    path: "/about",
    name: "about",
    components: {
      default: () =>
        import(/* webpackChunkName: "module_app" */ "./views/About"),
      sidebar: () =>
        import(
          /* webpackChunkName: "module_app" */ "./components/templates/sidebar"
        )
    },
    meta: {
      showNavHeader: true,
      showNavFooter: true,
      offline: true,
      name: "About",
      order: 2,
      description: "about me"
    }
  },
  {
    path: "/experience",
    name: "Experience",
    components: {
      default: () =>
        import(/* webpackChunkName: "module_app" */ "./views/Experience")
    },
    meta: {
      showNavHeader: true,
      showNavFooter: true,
      offline: true,
      name: "Experience",
      order: 3,
      description: "past works"
    }
  },
  {
    path: "/skills",
    name: "skills",
    components: {
      default: () =>
        import(/* webpackChunkName: "module_app" */ "./views/Skills"),
      sidebar: () =>
        import(
          /* webpackChunkName: "module_app" */ "./components/templates/sidebar"
        )
    },
    meta: {
      showNavHeader: true,
      showNavFooter: true,
      offline: true,
      name: "Skills",
      order: 4,
      description: "my skills"
    }
  },
  {
    path: "/contact",
    name: "contact",
    components: {
      default: () =>
        import(/* webpackChunkName: "module_app" */ "./views/Contact"),
      sidebar: () =>
        import(
          /* webpackChunkName: "module_app" */ "./components/templates/sidebar"
        ),
      offline: () =>
        import(/* webpackChunkName: "module_app" */ "./views/ContactOffline")
    },
    meta: {
      showNavHeader: true,
      showNavFooter: true,
      offline: true,
      name: "Contact",
      order: 100,
      description: "send a email"
    }
  }
];
