<template>
  <transition name="transition-translate" tag="div" appear>
    <slot></slot>
  </transition>
</template>

<script>
export default {
  name: 'TransitionTranslate'
};
</script>

<style lang="scss">
@import '@/assets/scss/abstract';

$component-name: 'transition-translate';

/* ==|== Transition-Translate =====================================
    GERAL
   ================================================================ */ 

.#{$component-name}-enter-active,
.#{$component-name}-leave-active {
  @include use-transition(get-transition(default));
}

.#{$component-name}-enter-active,
.#{$component-name}-leave-to {
  opacity: 0;
  @include use-transform(translateX(-100%));
}

.#{$component-name}-enter-to {
  opacity: 1;
  @include use-transform(translateX(0%));
}

.#{$component-name}-leave-active {
  position: absolute;
}
</style>
