<template>
  <ul class="special">
    <SpecialItem
      v-for="l in list"
      :key="l.id"
      :item="l"
      :selected="selected"
      @item-selected="handleSelected"
    >
      {{ l.content }}
    </SpecialItem>
    <!--
    <SpecialItem
      key="search"
      :item="{ id: 'search', icon: 'fas fa-search', content: '' }"
      :selected="selected"
      @item-selected="handleSelected"
    >
      <form>
        <input
          class="search-input"
          type="text"
          placeholder="Palavra + <enter>"
        />
      </form>
    </SpecialItem>
    -->
  </ul>
</template>

<script>
import SpecialItem from './SpecialItem';

export default {
  name: 'Special',
  components: { SpecialItem },
  data() {
    return {
      selected: ''
    };
  },
  computed: {
    list() {
      let list = [];
      //list.push({ id: 'user', icon: 'fas fa-user' });
      //list.push({ id:'cart', icon:'fas fa-shopping-cart', content:'R$ 0,00' });
      //list.push({ id:'notifications', icon:'fas fa-bell' });
      list.push({
        id: 'location',
        icon: 'fas fa-map-marker-alt',
        content: 'Aracatuba, Sao Paulo, Brazil'
      });
      list.push({
        id: 'mobile',
        icon: 'fas fa-mobile-alt',
        content: '+55 (18) 98135-4400'
      });
      list.push({
        id: 'email',
        icon: 'fas fa-envelope',
        content: 'webdevbrazil@gmail.com'
      });
      return list;
    }
  },
  methods: {
    handleSelected(item) {
      this.selected = this.selected !== item.id ? item.id : null;
    }
  }
};
</script>

<style lang="scss">
@import '@/assets/scss/abstract';

$component-name: 'special';

/* ==|== Special ==================================================
    GERAL
   ================================================================ */ 

ul.#{$component-name} {
  flex: 1;
  list-style: none;
  margin-top: 5px;
  margin-right: 10px;
  margin-bottom: 2px;
  padding-right: 5px;
  @include use-flex();
  justify-content: flex-end;
  @include use-transition(get-transition(default));
}

@include use-breakpoint(down, xs) {
  ul.#{$component-name} {
    justify-content: space-between;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    padding-bottom: 4px;
    margin-left: 6px;
  }
}
</style>
