<template>
  <footer>
    <div class="overlay-separator">
      <TrippleWaves3Negative />
    </div>
    <div class="container">
      <nav>
        <ul>
          <li v-for="(route, i) in routes" :key="i">
            <router-link
              exact
              class="menu"
              exact-active-class="active"
              :to="{ name: route.name }"
            >
              <i v-if="route.meta.icon" :class="route.meta.icon"></i>
              {{ route.meta.name }}
            </router-link>
          </li>
        </ul>
      </nav>
      <p>
        Copyright &copy; 2020. Todos direitos reservados.
      </p>
    </div>
  </footer>
</template>

<script>
import TrippleWaves3Negative from '@/modules/app/components/templates/layout/separators/TrippleWaves3Negative';

export default {
  name: 'Footer',
  components: { TrippleWaves3Negative },
  computed: {
    routes() {
      return this.$router.options.routes
        .filter(route => route.meta && route.meta.showNavFooter)
        .sort((a, b) => a.meta.order > b.meta.order);
    }
  }
};
</script>

<style src="./index.scss" lang="scss" />
