<template>
  <li class="special-item">
    <div class="icon" @click="itemSelected"><i :class="item.icon"></i></div>
    <div
      class="content"
      :class="{ selected: selected === item.id }"
      v-if="item.content !== undefined"
    >
      <slot></slot>
    </div>
  </li>
</template>

<script>
export default {
  name: 'SpecialItem',
  props: {
    item: {
      type: Object,
      required: true
    },
    selected: {
      type: String
    }
  },
  emits: ['item-selected'],
  setup(props, { emit }) {
    const itemSelected = () => {
      emit('item-selected', props.item);
    };
    return { itemSelected };
  }
};
</script>

<style lang="scss">
@import '@/assets/scss/abstract';

$component-name: 'special-item';

/* ==|== Special-Item =============================================
    GERAL
   ================================================================ */ 

.#{$component-name} {
  @include use-flex();
  justify-content: flex-start;
  @include use-transition(get-transition(default));
  z-index: get-z-index(normal);
}

.#{$component-name} .icon {
  color: get-color(bg);
  flex: 0;
  padding-top: 2px;
  margin-left: 10px;
  margin-right: 5px;
  cursor: pointer;
  text-shadow: 0 2px 4px get-color-opacity(bg-negative, 0.1);
  @include use-transition(get-transition(default));
}

.#{$component-name} .icon:hover {
  color: get-color(light);
}

.#{$component-name} .content {
  flex: 1;
  font-size: 0.9rem;
  color: get-color(bg);
  white-space: nowrap;
  width: 0px;
  visibility: hidden;
  opacity: 0;
  @include use-transition(get-transition(default));
}

.#{$component-name} .content.selected {
  width: 200px;
  opacity: 1;
  visibility: visible;
}

.#{$component-name} .content form {
  margin-top: -2px;
}

.#{$component-name} .content form .search-input {
  width: 120px;
  color: get-color(dark);
  font-size: 0.8rem;
  outline: 0;
  border: 0;
  border-radius: 12px;
  padding-left: 8px;
  padding-right: 8px;
}
</style>
