// sucess
export const FLASH_LOGIN_SUCCESS = {
  context: 'user',
  variant: 'success',
  message: 'You are logged now.'
};
export const FLASH_REFRESH_SUCCESS = {
  context: 'user',
  variant: 'success',
  message: 'Your previous sessions has been restaured.'
};

// info
export const FLASH_LOGOUT = {
  context: 'user',
  variant: 'info',
  message: 'Logout.'
};
export const FLASH_LOGIN_REQUIRED = {
  context: 'user',
  variant: 'info',
  message: 'Do login to access this page.'
};
export const FLASH_LOGGED = {
  context: 'user',
  variant: 'info',
  message: 'You are already logged.'
};
export const FLASH_TOKEN_REFRESH = {
  context: 'user',
  variant: 'info',
  message: 'Your token has been refreshed!'
};

// warning
export const FLASH_SESSION_EXPIRED = {
  context: 'user',
  variant: 'warning',
  message: 'Session expired. Do login again.'
};

// danger
export const FLASH_UNAUTHORIZED = {
  context: 'user',
  variant: 'danger',
  message: 'Access not authorized.'
};
export const FLASH_ERROR = err => {
  let context = 'user';
  let variant = 'danger';
  let message = err.error;

  if (err.data) {
    err.data.map(obj => {
      message = `${message} - ${obj.message}`;
    });
  }

  return { context, variant, message };
};
