<template>
  <transition-group name="transition-group-translate" tag="div" appear>
    <slot></slot>
  </transition-group>
</template>

<script>
export default {
  name: 'TransitionGroupTranslate'
};
</script>

<style lang='scss'>
@import '@/assets/scss/abstract';

$component-name: 'transition-group-translate';

/* ==|== Transition-Group-Translate ===============================
    GERAL
   ================================================================ */ 

.#{$component-name}-enter-active,
.#{$component-name}-leave-active {
  @include use-transition(get-transition(default));
}

.#{$component-name}-enter-active,
.#{$component-name}-leave-to {
  opacity: 0;
  @include use-transform(translateX(-100%));
}

.#{$component-name}-enter-to {
  opacity: 1;
  @include use-transform(translateX(0%));
}
</style>
