import * as types from './mutation-types';

export default {
  [types.MUTATION_USER_LOGIN_REQUEST](state) {
    state.userLoading = true;
    state.userInfo = {};
    state.userAuth = false;
    //state.userToken 	= undefined;
    state.userError = undefined;
  },

  [types.MUTATION_USER_LOGIN_SUCCESS](state, payload) {
    state.userLoading = false;
    state.userInfo = payload.user;
    state.userAuth = true;
    //state.userToken 	= payload.token;
    state.userError = undefined;
  },

  [types.MUTATION_USER_LOGIN_FAIL](state, payload) {
    state.userLoading = false;
    state.userInfo = {};
    state.userAuth = false;
    //state.userToken 	= undefined;
    state.userError = payload.error;
  },

  [types.MUTATION_USER_LOGOUT](state) {
    state.userLoading = false;
    state.userInfo = {};
    state.userAuth = false;
    state.userToken = undefined;
    state.userError = undefined;
  },

  [types.MUTATION_USER_SET_TOKEN](state, payload) {
    state.userToken = payload.token;
  },

  [types.MUTATION_USER_REFRESH_REQUEST](state) {
    state.userLoading = true;
    state.userError = undefined;
  },

  [types.MUTATION_USER_REFRESH_SUCCESS](state, payload) {
    state.userLoading = false;
    state.userAuth = true;
    state.userInfo = payload.user;
  },

  [types.MUTATION_USER_REFRESH_FAIL](state, payload) {
    state.userLoading = false;
    state.userError = payload.error;
  }
};
