<template>
  <Headline :medias="medias"> </Headline>
  <FlashMessages context="*" />
  <!--
  <ToastMessages />
  -->
  <section id="main">
    <div class="container">
      <main>
        <router-view v-if="showDefaultView" />

        <TransitionTranslate>
          <Offline v-if="showOfflineView" />
        </TransitionTranslate>

        <router-view
          v-slot="{ Component }"
          v-if="showCustomOfflineView"
          name="offline"
        >
          <TransitionTranslate>
            <component :is="Component" />
          </TransitionTranslate>
        </router-view>
      </main>

      <section id="sidebar" v-if="showSidebar">
        <router-view v-slot="{ Component }" name="sidebar">
          <TransitionTranslate>
            <component :is="Component" />
          </TransitionTranslate>
        </router-view>
      </section>
    </div>
  </section>
</template>

<script>
import { mapState } from 'vuex';

import TransitionTranslate from '@/modules/app/components/utils/TransitionTranslate';

import FlashMessages from '@/modules/app/components/features/FlashMessages';
import ToastMessages from '@/modules/app/components/features/ToastMessages';

import Headline from '@/modules/app/components/templates/layout/Headline';
import Offline from '@/modules/app/views/Offline';

export default {
  name: 'Main',
  components: {
    TransitionTranslate,
    FlashMessages,
    ToastMessages,
    Headline,
    Offline
  },
  computed: {
    ...mapState('app', ['appOnline']),
    showDefaultView() {
      return (
        this.appOnline || (!this.showOfflineView && !this.hasCustomOfflineView)
      );
    },
    showSidebar() {
      const hasSidebar =
        this.$route.matched[0] &&
        this.$route.matched[0].components &&
        this.$route.matched[0].components.sidebar !== undefined;
      return this.showDefaultView && hasSidebar;
    },
    showOfflineView() {
      return (
        !this.appOnline &&
        ((this.hasOfflineView && !this.hasCustomOfflineView) ||
          this.isOnlineExclusive)
      );
    },
    showCustomOfflineView() {
      return (
        !this.appOnline && this.hasOfflineView && this.hasCustomOfflineView
      );
    },
    isOnlineExclusive() {
      return this.$route.meta && this.$route.meta.privateLevel != undefined;
    },
    hasOfflineView() {
      return this.$route.meta && this.$route.meta.offline;
    },
    hasCustomOfflineView() {
      return (
        this.$route.matched[0] &&
        this.$route.matched[0].components &&
        this.$route.matched[0].components.offline !== undefined
      );
    },
    medias() {
      let list = [];
      list.push({
        _id: 1,
        title: 'Claudiney Calixto da Silva (Fullstack developer)',
        snapshot: '/medias/headlines/bg1.jpg',
        content: `
          The purpose of this site is to demonstrate some skills about me. The content is real, but some features is under construction.
          `
      });
      //list.push({ _id:2, title:'Head 2', snapshot:'/medias/headlines/bg2.jpg' });
      return list;
    }
  }
};
</script>

<style src="./index.scss" lang="scss" />
