const USER = 'user';
const TOKEN = 'token';
const REFRESH_TOKEN = 'refresh_token';

export const getLocalToken = () => localStorage.getItem(TOKEN);
export const setLocalToken = token => localStorage.setItem(TOKEN, token);
export const deleteLocalToken = () => localStorage.removeItem(TOKEN);

export const getLocalRefreshToken = () => localStorage.getItem(REFRESH_TOKEN);
export const setLocalRefreshToken = refreshToken =>
  localStorage.setItem(REFRESH_TOKEN, refreshToken);
export const deleteLocalRefreshToken = () =>
  localStorage.removeItem(REFRESH_TOKEN);

export const getLocalUser = () => {
  const user = localStorage.getItem(USER) || undefined;
  try {
    return JSON.parse(user);
  } catch (err) {
    return {};
  }
};

export const setLocalUser = user =>
  localStorage.setItem(USER, JSON.stringify(user));
export const deleteLocalUser = () => localStorage.removeItem(USER);
