<template>
  <Header :sticky="scrolled">
    <template #top>
      <ScrollProgress />
    </template>
    <template #brand>
      <Logo :sticky="scrolled" />
    </template>
    <template #info>
      <Special />
    </template>
    <template #default>
      <FlexMenu :sticky="scrolled" />
    </template>
  </Header>
  <teleport to="#modal-wrapper">
    <modal v-if="modalOpen" @close="toggleModalState">
      <p>Hello, I'm a modal window.</p>
    </modal>
  </teleport>
  <Main />
  <!--<button @click="toggleModalState">Open modal</button>-->
  <Footer />
</template>

<script>
import { ref } from 'vue';

import Modal from '@/modules/app/components/features/Modal';

// Header
import Header from '@/modules/app/components/templates/header';
import ScrollProgress from '@/modules/app/components/templates/layout/ScrollProgress';
import Logo from './modules/app/components/templates/layout/Logo';
import Special from './modules/app/components/templates/layout/special';
import FlexMenu from './modules/app/components/templates/layout/nav/FlexMenu';

// Main
import Main from './modules/app/components/templates/main';

// Footer
import Footer from './modules/app/components/templates/footer';

export default {
  setup() {
    const modalOpen = ref(false);
    const toggleModalState = () => {
      modalOpen.value = !modalOpen.value;
    };
    return {
      modalOpen,
      toggleModalState
    };
  },
  components: {
    Modal,
    Header,
    ScrollProgress,
    Logo,
    Special,
    FlexMenu,
    Main,
    Footer
  },
  data() {
    return {
      scrolled: false
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
  },
  beforeUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
      this.scrolled = window.scrollY > 80;
    }
  }
};
