import services from '@/http';
import * as storage from '../storage';
import * as types from './mutation-types';
import { flash } from '../resources';

export const ActionLogin = ({ dispatch, commit }, payload) => {
  commit(types.MUTATION_USER_LOGIN_REQUEST, payload);

  return new Promise((resolve, reject) => {
    //http.post('/user/auth', payload)
    services.user
      .login(payload)
      .then(res => {
        const { data } = res;
        storage.setLocalUser(data.user);
        //storage.setLocalToken(data.token);
        //storage.setLocalRefreshToken(data.refreshToken);

        commit(types.MUTATION_USER_LOGIN_SUCCESS, data);
        dispatch('ActionSetToken', res.data);
        dispatch('app/ActionAddFlash', flash.FLASH_LOGIN_SUCCESS, {
          root: true
        });
        resolve(data);
      })
      .catch(err => {
        const error = err.response ? err.response.data : { error: err.message };
        commit(types.MUTATION_USER_LOGIN_FAIL, error);
        dispatch('app/ActionAddFlash', flash.FLASH_ERROR(error), {
          root: true
        });
        reject(error);
      });
  });
};

const logoutStorage = () => {
  storage.deleteLocalUser();
  storage.deleteLocalToken();
  storage.deleteLocalRefreshToken();
};

export const ActionLogout = ({ commit, dispatch }) => {
  logoutStorage();
  commit(types.MUTATION_USER_LOGOUT);
  dispatch('app/ActionAddFlash', flash.FLASH_LOGOUT, { root: true });
  return Promise.resolve();
};

export const ActionSetToken = ({ commit }, payload) => {
  storage.setLocalToken(payload.token);
  storage.setLocalRefreshToken(payload.refreshToken);
  commit(types.MUTATION_USER_SET_TOKEN, payload);
  return Promise.resolve(payload);
};

export const ActionSessionExpired = ({ commit, dispatch }) => {
  logoutStorage();
  commit(types.MUTATION_USER_LOGOUT);
  dispatch('app/ActionAddFlash', flash.FLASH_SESSION_EXPIRED, { root: true });

  /*
  let redirect = router?.currentRoute?.value?.query?.r;
  if (!router.hasRoute(redirect)) { redirect = 'account'; }
  router.push({ name: redirect });
*/
  return Promise.resolve();
};

export const ActionSessionRefresh = ({ commit, dispatch }) => {
  commit(types.MUTATION_USER_REFRESH_REQUEST);

  return new Promise((resolve, reject) => {
    services.user
      .sessionRefresh()
      .then(res => {
        const { data } = res;
        storage.setLocalUser(data.user);
        commit(types.MUTATION_USER_REFRESH_SUCCESS, data);
        dispatch('app/ActionAddFlash', flash.FLASH_REFRESH_SUCCESS, {
          root: true
        });
        resolve(data.user);
      })
      .catch(err => {
        commit(types.MUTATION_USER_REFRESH_FAIL, err);
        //dispatch('ActionLogout');
        //console.log("Actions error:",err.response.data);
        reject(err);
      });
  });
};
