<template>
  <div
    class="flex-menu-toggle"
    :class="{ active: isOffCanvas }"
    @click="toggle"
  >
    <span class="bar"></span>
    <span class="bar"></span>
    <span class="bar"></span>
  </div>

  <nav
    class="flex-menu offcanvas-breakpoint"
    :class="{ sticky: isSticky, show: isOffCanvas }"
    role="navigation"
  >
    <ul>
      <li v-for="(route, i) in routes" :key="i">
        <router-link
          exact
          class="menu"
          exact-active-class="active"
          :to="{ name: route.name }"
        >
          <div>
            <i v-if="route.meta.icon" :class="route.meta.icon"></i>
            {{ route.meta.name }}
            <span>{{ route.meta.description }}</span>
          </div>
        </router-link>
      </li>
    </ul>
  </nav>
</template>
<script>
import { mapState, mapActions } from 'vuex';

export default {
  name: 'FlexMenu',
  data() {
    return {
      offcanvas: false
    };
  },
  props: {
    sticky: {
      type: Boolean,
      required: false
    }
  },
  computed: {
    ...mapState('app', ['appOffCanvas']),
    isOffCanvas() {
      return this.appOffCanvas;
    },
    isSticky() {
      return !this.isOffCanvas && this.sticky;
    },
    routes() {
      return this.$router.options.routes
        .filter(route => route.meta && route.meta.showNavHeader)
        .sort((a, b) => a.meta.order > b.meta.order);
    }
  },
  methods: {
    ...mapActions('app', ['ActionToggleOffCanvas']),
    toggle() {
      this.ActionToggleOffCanvas();
    }
  },
  mounted() {
    //console.log("nav mounted")
  }
};
</script>

<style src="./index.scss" lang="scss" />
