<template>
  <section id="headline">
    <div class="container">
      <div class="media-container" v-for="media in medias" :key="media._id" :style="{ 'background-image': `url(${media.snapshot})` }" :class="{ 'hide':!contentToggle }">        
        <div class="content" :class="{ 'hide':!contentToggle }">
          <div class="message">
            <h1>{{ media.title }}</h1>
            <p>{{ media.content }}</p>
          </div>
          <div class="toggle" @click="toggle"></div>
        </div>
        <div class="content-scroll" @click="scroll"></div>        
        <div class="overlay-color"></div>
        <div class="overlay-shape"></div>
        <div class="overlay-separator" :class="{ 'hide':!contentToggle }"><TrippleWaves2 /></div>
      </div>
    </div>
  </section>
</template>

<script>
import TrippleWaves2 from '../separators/TrippleWaves2';

export default {
  name: 'Headline',
  components: { TrippleWaves2 },
  data() {
    return {
      contentToggle: true
    }
  },
  props: {
    medias: {
      type: Array,
      required: true
    }
  },
  methods: {
    toggle() {
      console.log("Toggle headline...");
      this.contentToggle = !this.contentToggle;
    },
    scroll() {
      console.log("Scroll to content...");
    }
  }
};
</script>

<style src="./index.scss" lang="scss" />
