import { http } from '@/http';

export default {
  isOnline: () => {
    return http.get('/webapp', { timeout: 2500 });
  },
  findAllLanguages: () => {
  	return http.get('/webapp/languages');
  },
  findAllItems: () => {
    return http.get(`/items`);
  },
  findItemBySlug: slug => {
    return http.get(`/items/slug/${slug}`);
  }
};
