<template>
    <div class="offcanvas-panel" :class="{ show }">
        <div class="offcanvas-panel-header">
            <a href="#" class="logo">
              Webdesign site
            </a>
            <a href="#" class="offcanvas-btn offcanvas-btn-close"  @click="toggle">
              <i class="fas fa-times"></i>
            </a>                      
        </div>


        <ul class="offcanvas-panel-footer">
            <li><a href="#" data-offcanvas="#user" class="offcanvas-btn"><i class="fas fa-user"></i></a></li>
            <li><a href="#" data-offcanvas="#cart" class="offcanvas-btn"><i class="fas fa-shopping-cart"></i></a></li>
            <li class="offcanvas-breakpoint-btn"><a href="#" data-offcanvas="#flex-menu" class="offcanvas-breakpoint-btn"><i class="fas fa-bars"></i></a></li>
        </ul>
  </div>
  <b class="overlay-offcanvas" :class="{ show }" @click.stop="toggle"></b>
</template>

<script>
import { mapState, mapActions } from 'vuex';

export default {
    name: 'OffCanvas',
    computed: {
        ...mapState('app', ['appOffCanvas']),
        show() {
            return this.appOffCanvas;
        }
    },
    methods: {
        ...mapActions('app', ['ActionToggleOffCanvas']),
        toggle() {
            this.ActionToggleOffCanvas();
        }
    },      
}
</script>

<style src="./index.scss" lang="scss" />
