import services from '@/http';
import * as types from './mutation-types';
import { flash } from '@/resources';

export const ActionOnline = async ({ commit }) => {
  commit(types.MUTATION_ONLINE_REQUEST);
  return new Promise((resolve, reject) => {
    services.app
      .isOnline()
      .then(res => {
        commit(types.MUTATION_ONLINE_SUCCESS, res.data);
        resolve(res);
      })
      .catch(err => {
        const error = err.response ? err.response.data : { error: err.message };
        commit(types.MUTATION_ONLINE_FAIL, error);
        reject(error);
      });
  });
};

export const ActionOffline = ({ commit, dispatch }) => {
  commit(types.MUTATION_OFFLINE);
  dispatch('ActionAddFlash', flash.app.FLASH_OFFLINE);
  return Promise.resolve(false);
};

export const ActionAddFlash = ({ dispatch, commit }, payload) => {
  const flash = {
    id: `${Date.now()}-${Math.round(Math.random() * 1e9)}`,
    date: new Date(),
    context: payload.context,
    variant: payload.variant,
    message: payload.message
  };
  dispatch('ActionClearOldFlash');
  commit(types.MUTATION_ADD_FLASH, flash);
  return Promise.resolve(flash);
};

export const ActionRemoveFlash = ({ state, commit }, payload) => {
  const arr = state.appFlashMessages.filter(obj => obj.id !== payload.id);
  commit(types.MUTATION_SET_FLASH, arr);
  return Promise.resolve(arr);
};

export const ActionClearFlash = ({ commit }) => {
  commit(types.MUTATION_SET_FLASH, []);
  return Promise.resolve([]);
};

export const ActionClearOldFlash = ({ state, commit }) => {
  const expiresDate = new Date(new Date().getTime() - 0.5 * 1000);
  const arr = state.appFlashMessages.filter(obj => obj.date > expiresDate);
  commit(types.MUTATION_SET_FLASH, arr);
  return Promise.resolve(arr);
};

export const ActionToggleOffCanvas = ({ state, commit }) => {
  const payload = !state.appOffCanvas
  commit(types.MUTATION_SET_OFFCANVAS, payload);
  return Promise.resolve(payload);
};

