<template>
  <transition-group name="toast-list" tag="div" v-if="show" class="toast-list">
    <div
      v-for="toast in appToastMessages"
      :key="toast.id"
      class="toast-message"
    >
      <div class="title">{{ toast.title }}</div>
      <div class="message">{{ toast.message }}</div>
      <div class="close">
        <button @click="close(toast)"><i class="fas fa-times"></i></button>
      </div>
    </div>
  </transition-group>
</template>
<script>
import { mapState, mapActions } from 'vuex';

export default {
  name: 'ToastMessages',
  computed: {
    ...mapState('app', ['appToastMessages'])
  },
  methods: {
    ...mapActions('app', [
      'ActionAppRemoveToastMessages',
      'ActionAppClearToastMessages'
    ]),
    show() {
      return this.appToastMessages.length > 0;
    },
    close(toast) {
      this.ActionAppRemoveToastMessages(toast);
    }
  },
  beforeUnmount() {
    this.ActionClearToast();
  }
};
</script>

<style lang="scss" scoped>
@import '@/assets/scss/abstract';

$component-name: 'flash-message';

/* ==|== Toast-Message ============================================
    GERAL
   ================================================================ */ 

.toast-list {
  background: get-color-opacity(bg-negative, 0.3);
  z-index: 2;
  position: fixed;
  top: 0px;
  right: 0px;
  height: 100vh;
  width: 240px;
  padding-top: 100px;
  padding-right: 10px;
  @include use-flex();
}

.#{$component-name} {
  flex: 0;
  @include use-flex();
  padding: 5px;
  width: 100%;
  background: get-color-opacity(bg, 0.98);
  @include use-box-shadow(0 3px 3px 0 get-color-opacity(bg-negative, 0.3));
  border-radius: 4px;
  display: inline-block;
  margin-bottom: 20px;
}

.#{$component-name} .title {
  color: get-color(info);
  font-size: 1.2rem;
}

.#{$component-name} .message {
  flex: 0;
  color: get-color(dark);
  font-size: 1rem;
}

.#{$component-name} .close {
  order: 0;
}

.toast-list-enter-active,
.toast-list-leave-active {
  @include use-transition(all 0.4s);
}

.toast-list-enter,
.toast-list-leave-to {
  opacity: 0;
  @include use-transform(translateX(+100%));
}
</style>
